/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-block-editor/#useBlockProps
 */
import {
	useBlockProps,
	RichText,
	InspectorControls,
	ColorPalette,
} from "@wordpress/block-editor";

import { PanelBody, Button } from "@wordpress/components";

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import "./editor.scss";

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */
export default function Edit({ attributes, setAttributes }) {
	const { title, body, bodyColor } = attributes;

	function onBodyColorChange(newColor) {
		setAttributes({ bodyColor: newColor });
	}

	function onChangeTitle(newTitle) {
		setAttributes({ title: newTitle });
	}

	function onChangeBody(newBody) {
		setAttributes({ body: newBody });
	}

	return (
		<div style={{ backgroundColor: bodyColor }} {...useBlockProps()}>
			<InspectorControls style={{ marginBottom: "40px" }}>
				<PanelBody title={"Block Color Settings"}>
					<p>
						<strong>Select a block color:</strong>
					</p>
					<ColorPalette
						value={bodyColor}
						onChange={onBodyColorChange}
					></ColorPalette>
				</PanelBody>
			</InspectorControls>
			<div class="block-wrapper" style={{ backgroundColor: bodyColor }}>
				<RichText
					key="editable"
					tagName="h3"
					placeholder="Title"
					value={title}
					onChange={onChangeTitle}
				/>
				<RichText
					key="editable"
					tagName="p"
					placeholder="Content"
					value={body}
					onChange={onChangeBody}
				/>
			</div>
		</div>
	);
}
